<template>
    <div class="layout-px-spacing">
        <teleport to="#breadcrumb">
            <ul class="navbar-nav flex-row">
                <li>
                    <div class="page-header">
                        <nav class="breadcrumb-one" aria-label="breadcrumb">
                            <ol class="breadcrumb">
                                <li class="breadcrumb-item"><a href="javascript:;">DataTables</a></li>
                                <li class="breadcrumb-item active" aria-current="page"><span>Bulk Statement</span></li>
                            </ol>
                        </nav>
                    </div>
                </li>
            </ul>
        </teleport>

        <div class="seperator-header layout-top-spacing">
        </div>
        <button v-if="!user_role == 8 " class="btn btn-dark me-2 px-3" @click="$router.go(-1)">
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-arrow-left" viewBox="0 0 16 16">
  <path fill-rule="evenodd" d="M15 8a.5.5 0 0 0-.5-.5H2.707l3.147-3.146a.5.5 0 1 0-.708-.708l-4 4a.5.5 0 0 0 0 .708l4 4a.5.5 0 0 0 .708-.708L2.707 8.5H14.5A.5.5 0 0 0 15 8"/>
</svg>
                </button>
                
        <div class="row layout-top-spacing" >
            <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
                <div class="panel p-0">
                    <div class="custom-table table3" >
                              <div v-if="loading_spinner" v-html="tableShimmer()"></div>         
                        <v-client-table v-else :data="bulk" :columns="columns" :options="table_option">
                            <template #actions="props">
                                <router-link :to="'/bulk-distribution/' + props.row.store_id" class="text-primary">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-eye" viewBox="0 0 16 16">
                                      <path d="M16 8s-3-5.5-8-5.5S0 8 0 8s3 5.5 8 5.5S16 8 16 8M1.173 8a13 13 0 0 1 1.66-2.043C4.12 4.668 5.88 3.5 8 3.5s3.879 1.168 5.168 2.457A13 13 0 0 1 14.828 8q-.086.13-.195.288c-.335.48-.83 1.12-1.465 1.755C11.879 11.332 10.119 12.5 8 12.5s-3.879-1.168-5.168-2.457A13 13 0 0 1 1.172 8z"/>
                                      <path d="M8 5.5a2.5 2.5 0 1 0 0 5 2.5 2.5 0 0 0 0-5M4.5 8a3.5 3.5 0 1 1 7 0 3.5 3.5 0 0 1-7 0"/>
                                    </svg>
                                </router-link>
                            </template>
                        </v-client-table>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<style>
    .table3 .actions svg {
        padding: 2px;
    }
</style>

<script setup>
    import { onMounted, ref,inject,computed } from 'vue';
    import { useMeta } from '@/composables/use-meta';
    import axios from "axios";
    useMeta({ title: 'Bulk Statement' });
    const user_role = JSON.parse(localStorage.getItem("user")).role;
    const columns = ref(['Sno' ,'project','store','petrol_used','diesel_used','diesel_balance','petrol_balance','actions',]);
    const loading_spinner = ref(false); 
    const showMessage = inject('showMessage');
    const showError = inject('showError');
    const tableShimmer = inject('tableShimmer') ;    
    const table_option = ref({
        perPage: 20,
        perPageValues: [  20, 50 , 100],
        skin: 'table table-hover table-striped',
        columnsClasses: { actions: 'actions text-center' },
        sortable: ['SNo' ,'project',  ],
        sortIcon: {
            base: 'sort-icon-none',
            up: 'sort-icon-asc',
            down: 'sort-icon-desc',
        },
        pagination: { nav: 'scroll', chunk: 5 },
        texts: {
            count: 'Showing {from} to {to} of {count}',
            filter: '',
            filterPlaceholder: 'Search...',
            limit: 'Results:',
        },
        resizableColumns: false,
    });

    const axiosInstance = axios.create({

        baseURL:process.env.VUE_APP_API_BASE_URL
    });




  const bulk = ref([])
 const getBulk = async () => {
   loading_spinner.value = true;     
   const response = await axiosInstance.get('get-fuel')
   bulk.value = response.data.map((item,index)=>({
    ...item,
    Sno : index + 1,
    store_id: item.store_id,
    project: item.project_name,
    store: item.store_name,
    diesel_quantity: item.diesel_quantity,
    petrol_quantity: item.petrol_quantity,
    petrol_used: item.petrol_used,
    diesel_used: item.diesel_used,
    diesel_balance: item.diesel_quantity - item.diesel_used,
    petrol_balance: item.petrol_quantity - item.petrol_used
   }))

   loading_spinner.value = false;     
};

// Helper function for consistent date formatting
function formatDate(dateString) {
    return new Date(dateString).toLocaleDateString('en-GB') || 'N/A';
}



    onMounted(() => {
        getBulk();    
    });

</script>
